html {
  font-family: 'Roboto', 'Open Sans', sans-serif;
}

* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
}

.container {
  padding: 30px 60px;
  width: 100%;
  height: 100vh;
  background: url(images/background_beer.png) repeat;
  background-size: 100px 100px;
  z-index: -1;

  &:after {
    content: "";
    width: 100%;
    height: 100%;
    background-color: rgba(20, 20, 20, 0.897);
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    z-index: 1;
  }
}



img {
  width: 100px;
  height: 100px;
}

h4 {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 20px;
  color: #fefeff;
}

h5 {
  @extend h4;
}

p, span {
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 20px;
  color: #fefeff;
}

.beer-item-title {
  color: #fefeff;
}